import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { IJobTitleSearchVM } from "app/admin/models/setup/job-title";

@Injectable({
  providedIn: "root",
})
export class JobTitleService {
  constructor(private _HttpClient: HttpClient) {}

  getAllJobTitles(data: IJobTitleSearchVM,eduCompId:number,page:number) {
    return this._HttpClient.post(
      `${environment.apiURL}jobTitle/getAllJobTitle/${eduCompId}/${page}`,
      data
    );
  }

  addJobTitle<T>(jobTitle: T) {
    return this._HttpClient.post<T>(
      `${environment.apiURL}jobTitle/PostJobTitle`,
      jobTitle
    );
  }

  updateJobTitles<T>(jobTitle: T, id: number) {
    return this._HttpClient.put<T>(
      `${environment.apiURL}jobTitle/PutJobTitle/${id}`,
      jobTitle
    );
  }

  deleteJobTitles(id: number) {
    return this._HttpClient.delete(
      `${environment.apiURL}jobTitle/DeleteJobTitle/${id}`
    );
  }

  getJobTitleByDepartment<T>(id: number) {
    return this._HttpClient.get<T>(
      `${environment.apiURL}jobTitle/getJobTitlesByDepartmentId/${id}`
    );
  }

  getSpecificJobTitlesByDepartmentIds(ids: any) {
    return this._HttpClient.post(
      `${environment.apiURL}jobTitle/getSpecificJobTitlesByDepartmentIds`,
      ids
    );
  }

  deleteJobTitleDepartment(id: number) {
    return this._HttpClient.delete(
      `${environment.apiURL}jobTitle/DeleteDepartmentOfJobTitle/${id}`
    );
  }

  update_JobTitle_Department<T>(body: T, id: number) {
    return this._HttpClient.put(
      `${environment.apiURL}jobTitle/EditDepartmentOfJobTitle/${id}`,
      body
    );
  }
}
