import { DatePipe } from "@angular/common";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  GetJob,
  GetJobDepartment,
  JobTitleByDepartment,
  JopDepViewModel,
} from "app/admin/models/setup/job-title-by-department";
import { DepartmentService } from "app/admin/services/setup/department.service";
import { JopTitleDepartmentService } from "app/admin/services/setup/job-title-department.service";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { UpdateDateComponent } from "app/shared/components/dialogs/update-date/update-date.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

import { IDepartmentModel } from "../../../models/setup/department";
import { AuthService } from "./../../../../shared/services/auth/auth.service";

@Component({
  selector: "app-deparment-jop-title",
  templateUrl: "./deparment-job-title.component.html",
  styleUrls: ["./deparment-job-title.component.scss"],
})
export class UserDeparmentAndJobTitleComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  curLang: string;
  itemsCounts: number = 0;
  departments: IDepartmentModel[] = [];
  joblist: GetJob[] = [];
  alljoblist: GetJobDepartment[] = [];
  displayedColumns: string[] = [
    "id",
    "EnglishDepartmentName",

    "ArabicDepartmentName",
    "JobTitle",
    "options",
  ];
  dataSource: MatTableDataSource<JobTitleByDepartment>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  currentDate = new Date();

  functionId: number;
  EduCompId: number;
  functionParentId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "UpdateArticalJob" },
  //   { functionName: "DeleteArticalJob" },
  // ];
  userId: number;
  username: string;
  jopTitle = [];
  dep_id: number = 0;
  pageNum: number = 0;
  initTable: boolean = true;
  deptsForUser: JobTitleByDepartment[];

  constructor(
    private fb: FormBuilder,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private _serjopTitlebyDepartment: JopTitleDepartmentService,
    private toastr: ToastrService,
    private _Serdep: DepartmentService,
    private _serjob: JobTitleService,
    private route: ActivatedRoute,
    private dialog: MatDialog,
    public authserv: AuthService,
    private datePipe: DatePipe
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    //get userid
    route.paramMap.subscribe((params) => {
      this.userId = Number(params.get("userId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.functionParentId = Number(params.get("parentId"));
      this.username = params.get("username");
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getall(1);
    this.getDepartmentsDropDown();
    this.myForm = this.fb.group({
      dept_id: [0, [Validators.required]],
      job_departments: [
        {
          value: "",
          disabled: true,
        },
        [Validators.required],
      ],
      from_date: [null],
      to_date: [null],
    });
  }

  ngAfterViewInit() {
    // this.dataSource.sort = this.sort;
    // this.dataSource.paginator = this.paginator;

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getDepartmentsDropDown();
    });
  }

  get departmentCtrl() {
    return this.myForm.controls;
  }

  applyFilter(e: string) {
    this.dataSource.filter = e.trim().toLowerCase();
  }

  customFilter() {
    this.dataSource.filterPredicate = (data: JobTitleByDepartment, filter) => {
      return (
        data.dept_ar_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.dept_en_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.job_departments
          ?.map((d) => d.job_ar_name)
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1 ||
        data.job_departments
          ?.map((d) => d.job_en_name)
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1
      );
    };
  }

  getDepartmentsDropDown() {
    this._Serdep
      .getDepartmentsDropDown(this.EduCompId)
      .subscribe((res: any) => {
        if (res) {
          this.departments = res;
        }
      });
  }

  getJobTitleByDepartment(id: number) {
    this.departmentCtrl["job_departments"]?.setValue("");
    if (this.departmentCtrl["dept_id"]?.value) {
      this._serjob
        //id=> id department
        .getJobTitleByDepartment<GetJob>(id)
        .subscribe((res: any) => {
          if (res) {
            this.joblist = res;

            this.departmentCtrl["job_departments"].enable();
          } else this.departmentCtrl["job_departments"].disable();
        });
    }
  }

  getall(page: number) {
    this.pageNum = page;
    this.spinner.show();
    return this._serjopTitlebyDepartment
      .getJopTitlebyDepartment<JopDepViewModel>(this.userId, page)
      .subscribe((data) => {
        if (data) {
          this.dataSource = new MatTableDataSource(
            data.userDepartmentJobtitleList
          );
          this.itemsCounts = data.itemsCounts;
          this.deptsForUser = data.userDepartmentJobtitleList;
          // console.log(this.deptsForUser)
          this.initTableItems();
          this.customFilter();
        }
        this.spinner.hide();
      });
  }

  initTableItems() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "job_departments":
          return this.curLang == "ar"
            ? item.job_departments[0]?.job_ar_name
            : item.job_departments[0]?.job_en_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
    // paginator
    if (this.initTable) {
      this.dataSource.paginator = this.paginator;
      this.initTable = false;
    }
  }

  addAll() {
    if (
      this.departmentCtrl["to_date"].value &&
      this.departmentCtrl["from_date"].value >
        this.departmentCtrl["to_date"].value
    ) {
      this.toastr.error("       ");
    } else {
      this.submitted = true;
      const model = {
        user_id: this.userId,
        jobTitle_dept_id: this.departmentCtrl["job_departments"].value,
        from_date: this.datePipe.transform(
          this.myForm.value.from_date ?? this.currentDate
        ),
        to_date: this.datePipe.transform(this.myForm.value.to_date),
      };
      // console.log("ff", model);
      if (this.myForm.valid) {
        this.spinner.show();
        this._serjopTitlebyDepartment
          .addJopTitlebyDepartment(model)
          .subscribe((res: any) => {
            if (res.returnValue == 1) {
              console.log("cc", model);
              this.submitted = false;
              this.getall(this.pageNum);
              this.toastr.success("   ");
              this.myForm.reset();
              this.departmentCtrl["dept_id"]?.setValue(0);
              // console.log(this.myForm.value);
            } else this.toastr.error(res.returnString);
          });
      }
      this.spinner.hide();
    }
  }

  updetAll() {
    if (
      this.departmentCtrl["to_date"].value &&
      this.departmentCtrl["from_date"].value >
        this.departmentCtrl["to_date"].value
    ) {
      this.toastr.error("       ");
    } else {
      this.submitted = true;
      let allDepJobIds: number[] = this.departmentCtrl["job_departments"].value;
      this.deptsForUser.forEach((dep) => {
        if (dep.dept_id != this.dep_id) {
          dep.job_departments.forEach((job) =>
            allDepJobIds.push(job.dep_job_id)
          );
        }
      });
      // console.log(allDepJobIds)
      const model = {
        user_id: this.userId,
        jobTitle_dept_id: allDepJobIds,
        from_date: this.datePipe.transform(this.myForm.value.from_date),
        to_date: this.datePipe.transform(this.myForm.value.to_date),
      };
      if (this.myForm.valid) {
        this.spinner.show();
        this._serjopTitlebyDepartment
          .editAllDeparment(model, this.departmentCtrl["dept_id"].value)
          .subscribe((res: any) => {
            if (res.returnValue == 1) {
              this.submitted = false;
              this.getall(this.pageNum);
              this.toastr.success("   ");
              this.myForm.reset();
              this.departmentCtrl["dept_id"].setValue(0);
              this.dep_id = 0;
            } else this.toastr.error(res.returnString);
          });
      }
      this.spinner.hide();
    }
  }

  editAll(department: JobTitleByDepartment) {
    //  this.myForm.patchValue(department);
    this.dep_id = department.dept_id;
    this.departmentCtrl["dept_id"].setValue(department.dept_id);
    this.getJobTitleByDepartment(this.departmentCtrl["dept_id"].value);
    this.departmentCtrl["job_departments"]?.setValue(
      department.job_departments.map((j) => j.dep_job_id)
    );
  }

  editDate(editjobTitle: GetJobDepartment) {
    this.dialog
      .open(UpdateDateComponent, {
        data: {
          user_dept_job_id: editjobTitle.user_dept_job_id,
          from_date: editjobTitle.from_date,
          to_date: editjobTitle.to_date,
        },
      })
      .afterClosed()
      .subscribe((res) => {
        console.log(res);
        if (res) this.getall(this.pageNum);
      });
  }

  deleteUserDeptJobTitle(depId: JobTitleByDepartment) {
    this._serjopTitlebyDepartment
      .deleteUserDeptJobTitle(this.userId, depId.dept_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == depId);
          this.dataSource.data.splice(i, 1);
          //this.dataSource.paginator = this.paginator;
          this.translate
            .get("categories.toasterdeleteSucses")
            .subscribe((data) => {
              this.toastr.success(data);
              this.getall(this.pageNum);
              this.myForm.reset();
              this.departmentCtrl["dept_id"]?.setValue(0);
              this.submitted = false;
            });
        } else this.toastr.error(res.returnString);
      });
  }

  openDeleateDialog(depId: JobTitleByDepartment): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? depId.dept_en_name : depId.dept_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUserDeptJobTitle(depId);
      });
  }

  deleteJopTitle(jopid: GetJobDepartment) {
    this._serjopTitlebyDepartment
      .deleteJopTitle(jopid.user_dept_job_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.translate
            .get("categories.toasterdeleteSucses")
            .subscribe((data) => {
              this.toastr.success(data);
              this.getall(this.pageNum);
              this.myForm.reset();
              this.departmentCtrl["dept_id"]?.setValue(0);
              this.submitted = false;
            });
        } else this.toastr.error(res.returnString);
      });
  }

  openDeleateDialogJop(jopid: GetJobDepartment): void {
    // console.log("id", jopid.user_dept_job_id);
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? jopid.job_ar_name : jopid.job_en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteJopTitle(jopid);
      });
  }
}
