import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root",
})
export class DepartmentService {
  constructor(private http: HttpClient) {}

  getAllDepartments<T>(page: number, EduCompId: number) {
    return this.http.get<T>(
      `${environment.apiURL}Department/getDepartments/${EduCompId}/${page}`
    );
  }

  getDepartmentsDropDown<T>(EduCompId: number) {
    return this.http.get<T>(
      `${environment.apiURL}Department/getAllDepartments/${EduCompId}`
    );
  }

  // addEditDepartment<T, TV>(obj: T) {

  //   return this.http.post<TV>(`${environment.apiURL}Department/PostDepartments`, obj);
  // }

  getDepartmentById<T>(id: number) {
    return this.http.get<T>(`${environment.apiURL}Department/${id}`);
  }

  addDepartment<T>(department: T) {
    return this.http.post<T>(
      `${environment.apiURL}Department/PostDepartments`,
      department
    );
  }

  editDepartment<T>(department: T, id: number) {
    return this.http.put<T>(
      `${environment.apiURL}Department/PutOneDepartments/${id}`,
      department
    );
  }

  deleteDepartment(departmentId: number) {
    return this.http.delete(
      `${environment.apiURL}Department/DeleteOneDepartments/${departmentId}`
    );
  }
}
